.flash-content {
  width: 50rem;
  height: 50rem;
  border-radius: 50%;
  z-index: 1000;
  margin: auto;
  animation-name: theFlash;
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  position: absolute; }

@keyframes theFlash {
  0% {
    transform: scale(0.5);
    background-color: #77cb5c; }
  50% {
    transform: scale(8);
    background-color: #e0bf24; }
  100% {
    transform: scale(0.001);
    display: none;
    background-color: #77cb5c; } }

