.alert-warning {
  background-color: #ff0f0f; }

.floating-alert {
  display: none;
  position: absolute;
  z-index: 600;
  bottom: -5rem;
  right: 3rem;
  padding: 1rem 2rem;
  font-size: 1.6rem;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: floatingAlert ease-in-out 4s forwards; }

@keyframes floatingAlert {
  0% {
    opacity: 0;
    visibility: hidden; }
  20% {
    opacity: 1;
    visibility: visible; }
  100% {
    opacity: 0;
    visibility: hidden;
